import { useState, useEffect } from "react";
import axios from "axios";

const useETHPrice = () => {
  const [price, setPrice] = useState(null);  // Stocm preul ETH
  const [loading, setLoading] = useState(true);  // Starea de ncrcare
  const [error, setError] = useState(null);    // Starea pentru erori

  useEffect(() => {
    const fetchETHPrice = async () => {
      try {
        setLoading(true);
        const response = await axios.get(
          "https://api.coingecko.com/api/v3/simple/price?ids=ethereum&vs_currencies=usd"
        );
        setPrice(response.data.ethereum.usd); // Setm preul ETH
      } catch (err) {
        setError("Failed to fetch ETH price");
      } finally {
        setLoading(false);
      }
    };

    fetchETHPrice();
    const interval = setInterval(fetchETHPrice, 60000);  // Actualizeaz preul la fiecare 60 secunde

    return () => clearInterval(interval);  // Cur intervalul cnd componenta se demonteaz
  }, []);

  return { price, loading, error };
};

export default useETHPrice;
